$logo-width: 115px;
$logo-height: 76px;
$active-color: #9fadc2;
$hover-duration: 0.2s;
$small-screen-breack-point: 999px;

.header {
  height: 700px;
  background-image: url("../images/header_new.svg");
  background-size: cover;
  background-position: center;
  position: relative;
  font-weight: bold;
  line-height: 16px;
}

.header__name {
  position: absolute;
  top: 314px;
  left: 120px;
  bottom: 165px;
  font-size: 60px;
  line-height: 60px;
  letter-spacing: -1.5px;
  color: white;
}

.header__name::before {
  content: "";
  display: block;
  width: 35px;
  height: 2px;
  border-radius: 2px;
  background: white;
  margin-bottom: 16px;
}

.header__nav {
  position: absolute;
  bottom: 48px;
  left: 0;
  right: 0;
}

.header__top {
  position: relative;
  display: flex;
  justify-content: space-between;
  top: 24px;
  color: white;
}

.header__phone {
  margin-left: 120px;
}

.header__action {
  margin-right: 120px;
}

.header__phone,
.header__action {
  position: relative;
  top: 32px;
}

.header__link {
  color: white;
  text-decoration: none;
  border-bottom: 1px solid white;
  transition: color $hover-duration;
  text-transform: uppercase;
}

.header__link:hover {
  color: $active-color;
}

@media screen and (max-width: $small-screen-breack-point) {
  .header__top {
    flex-direction: column;
  }
}
